.interface {
  display: flex;
  height: 100vh
}
.inteface__goods {
  background: #9dcfaa;
  flex: 1 1 auto;
}
.interface__control-panel {
  background: #9dcfaa;
  padding-right: 150px;
  min-width: 300px;
}
.product-list {
  display: flex;
  flex-wrap: wrap;
}
input {
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  border-radius: 4px;
  height: 25px;
  padding: 10px;
  font-weight: bold;
}
.product-list__item {
  list-style-type: none;
  margin: 10px;
  flex: 0 0 calc(25% - 0.5rem);
}
.product {
  flex-direction: column;
  display: flex;
  font-weight: bold;
  padding: 13px;
  background: white;
  margin-bottom: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  border-radius: 4px;
  min-width: 76px;
}
.dialog-board {
  background: white;
  font-weight: bold;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  border-radius: 4px;
  margin-bottom: 10px;
  margin-top: 25px;
  padding: 20px;
  min-height: 30px;
}
.form__desc {
  width: 200px;
}
.conclusion {
  display: flex;
  justify-content: space-between;
}
.result {
  height: 90px;
  flex: 1 1 auto;
  margin: 10px 0 10px 10px;
  justify-content: space-between;
}